import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  Paper
} from '@material-ui/core';


class About extends React.Component {
    render() {
        return (
          <Paper elevation={3}>
            <Card variant="outlined">
                <CardContent>
                <Typography variant="h5" component="h2">
                COMMUNICATIONS STYLE INVENTORY
                </Typography>
                <Typography color="textSecondary">
                This is an informal survey, designed to 
        determine how you usually act in everyday 
        related situations.  The idea is to get a 
        clear description of how you see yourself.  
        On the answer sheet, circle A or B in ea
        ch pair of statements below, which shows 
        the one that MOST, describes you.
                </Typography>
                </CardContent>
            </Card>
          </Paper>
        )
    }
}
export default About;