import React from 'react';
import {
  Button,
  List,
  ListItem,
  Card,
  CardContent,
  Typography,
  Paper,
  TextField
} from '@material-ui/core';
import Question from "./Question"

function Questions({communicationStyleData}) {
  const [answers, setAnswers] = React.useState({});
  const [name, setName] = React.useState("Unknown");
  let questions = Object.keys(communicationStyleData.questions)
  .sort((a, b) => Number(a) - Number(b))
  .map(questionNumber => {
    return (
      <ListItem key={questionNumber} divider>
        <Question
          number={questionNumber} 
          A={communicationStyleData.questions[questionNumber].A} 
          B={communicationStyleData.questions[questionNumber].B}
          clicked={setSelectedOption(questionNumber)}/>
      </ListItem>
    )
  })

  function setSelectedOption(number){
    return function(selectedAnswer){
      let newSelection = {}
      newSelection[number] = selectedAnswer;
      setAnswers(previouAnswers => ({ ...previouAnswers, ...newSelection}))
    }
  }

  function updateName(name){
    setName(name)
  }

  return (
    <Paper elevation={3}>
        <Card variant="outlined">
            <CardContent>
                <Typography variant="h5" component="h2">
                COMMUNICATIONS STYLE INVENTORY
                </Typography>
                <Typography color="textSecondary">
                This is an informal survey, designed to 
        determine how you usually act in everyday 
        related situations.  The idea is to get a 
        clear description of how you see yourself.  
        On the answer sheet, circle A or B in ea
        ch pair of statements below, which shows 
        the one that MOST, describes you.
                </Typography>
            </CardContent>
        </Card>

        <Card variant="outlined">
            <CardContent>
                <Typography variant="h5" component="h2">
                Name
                </Typography>
                <TextField id="outlined-basic" label="Outlined" variant="outlined" onChange={(event) => updateName(event.target.value)} />
            </CardContent>
        </Card>

        <List>
            {questions}
        </List>
        
        <Button
            variant="contained"
            color="primary"
            onClick={() => {
                if(Object.keys(answers).length < Object.keys(communicationStyleData).length){
                    alert("Please complete all questions!")
                }
                else {
                    let results = {answers, name: name}
                    let objJsonStr = JSON.stringify(results);
                    let objJsonB64 = btoa(objJsonStr);
                    //alert(objJsonB64)
                    window.location = `${process.env.PUBLIC_URL}/results?data=${objJsonB64}`
                }
            }
            }
        >
            Submit
        </Button>
    </Paper>
  );
}

export default Questions;
