import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  Paper,
  ListItem,
  List
} from '@material-ui/core';
import {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton,
  TelegramShareButton,
  WhatsappShareButton,
  PinterestShareButton,
  VKShareButton,
  OKShareButton,
  RedditShareButton,
  TumblrShareButton,
  LivejournalShareButton,
  MailruShareButton,
  ViberShareButton,
  WorkplaceShareButton,
  EmailShareButton,
} from 'react-share';

import {
  FacebookIcon,
  TwitterIcon,
  TelegramIcon,
  WhatsappIcon,
  GooglePlusIcon,
  LinkedinIcon,
  PinterestIcon,
  VKIcon,
  OKIcon,
  RedditIcon,
  TumblrIcon,
  LivejournalIcon,
  MailruIcon,
  ViberIcon,
  WorkplaceIcon,
  EmailIcon,
} from 'react-share';

function ResultOutput(props) {
  let bulletList  = props.resultInfo.bullets.map((bullet, index) => {
    return (
      <ListItem key={index} divider>
        {bullet}
      </ListItem>
    )
  })

  return (
    <Card variant="outlined">
        <CardContent>
          <Typography color="textSecondary">
          {`${props.resultInfo.type}`}
          </Typography>
          <List>
            {bulletList}
          </List>
          <FacebookShareButton url={window.location}>
            <FacebookIcon size={32} round={true} />
          </FacebookShareButton>

          <LinkedinShareButton url={window.location} >
            <LinkedinIcon size={32} round={true} />
          </LinkedinShareButton>

          <TwitterShareButton url={window.location} > 
            <TwitterIcon size={32} round={true} />
          </TwitterShareButton>

          <TelegramShareButton url={window.location} > 
            <TelegramIcon size={32} round={true} />
          </TelegramShareButton>

          <WhatsappShareButton url={window.location} >  
            <WhatsappIcon size={32} round={true} />
          </WhatsappShareButton>

          <PinterestShareButton url={window.location} >  
            <PinterestIcon size={32} round={true} />
          </PinterestShareButton>

          <VKShareButton url={window.location} >  
            <VKIcon size={32} round={true} />
          </VKShareButton>

          <OKShareButton url={window.location} >  
            <OKIcon size={32} round={true} />
          </OKShareButton>

          <RedditShareButton url={window.location} >  
            <RedditIcon size={32} round={true} />
          </RedditShareButton>

          <TumblrShareButton url={window.location} >  
            <TumblrIcon size={32} round={true} />
          </TumblrShareButton>

          <LivejournalShareButton url={window.location} >  
            <LivejournalIcon size={32} round={true} />
          </LivejournalShareButton>

          <MailruShareButton url={window.location} >  
            <MailruIcon size={32} round={true} />
          </MailruShareButton>

          <ViberShareButton url={window.location} >  
            <ViberIcon size={32} round={true} />
          </ViberShareButton>

          <WorkplaceShareButton url={window.location} >  
            <WorkplaceIcon size={32} round={true} />
          </WorkplaceShareButton>

          <EmailShareButton url={window.location} >  
            <EmailIcon size={32} round={true} />
          </EmailShareButton>
        </CardContent>
    </Card>
  )
}

function Results({communicationStyleData}) {

  let search = window.location.search;
  let params = new URLSearchParams(search);
  let data = params.get('data');

  let dataJSON = validatedData(data)
  let keyCount = [
    {"key": "O", "count": getCount("O", dataJSON.answers)},
    {"key": "G", "count": getCount("G", dataJSON.answers)},
    {"key": "D", "count": getCount("D", dataJSON.answers)},
    {"key": "I", "count": getCount("I", dataJSON.answers)}
  ].sort((a, b) => b.count - a.count)

  let resType = [keyCount[0].key, keyCount[1].key].sort().join("")

  function validatedData(data){
    let dataJSON = {}
    try {
      dataJSON = JSON.parse(atob(data))
    } catch(err){
      console.log(err)
    }

    return dataJSON
  }

  function getCount(key, answers) {
    let count = 0;
    let expectedAnswers = communicationStyleData.groupResults[key]
    for(let question in expectedAnswers){
      if(expectedAnswers[question] === answers[question]){
        count++;
      }
    }

    return count;
  }

  console.log(communicationStyleData.resultInfos)
  console.log(resType)
  console.log(communicationStyleData.resultInfos[resType])

  return (
    <Paper elevation={3}>
        <Card variant="outlined">
            <CardContent>
            <Typography variant="h4" component="h2">
              {`Results for: ${dataJSON.name || "Unknown"}`}
            </Typography>
            </CardContent>
        </Card>
        <ResultOutput resultInfo={communicationStyleData.resultInfos[resType]}/>
        {/* <Card variant="outlined">
            <CardContent>
            <Typography color="textSecondary">
            {`${JSON.stringify(keyCount)}`}
            </Typography>
            </CardContent>
        </Card> */}
    </Paper>
  );
}

export default Results;
