import React from 'react';
import {
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel
} from '@material-ui/core';
import { makeStyles, withStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

class Question extends React.Component {
    render() {
        return (
            <FormControl /*className={this.props.classes.root}*/ component="fieldset">
                <FormLabel component="legend">Question# {this.props.number}</FormLabel>
                <RadioGroup aria-label="option" name="customized-radios" onChange={(event) => this.props.clicked(event.target.value)}>
                    <FormControlLabel value="A" control={<Radio />} label={this.props.A}/>
                    <FormControlLabel value="B" control={<Radio onClick={() => this.props.clicked("B")}/>} label={this.props.B}/>
                </RadioGroup>
            </FormControl>
        )
    }
}
// export default withStyles(useStyles)(Question);
export default Question;